.app__aboutus {
  position: relative;
}

.app__aboutus-overlay {
  position: absolute;
  inset: 0;
}

.app__aboutus-overlay img {
  width: 391px;
  height: 415px;
  z-index: 0;
}

.app__aboutus-content {
  width: 100%;
  z-index: 2;
}

.app__aboutus-content_about {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-end;
  
  text-align: right;
}

.app__aboutus-content_knife img {
  height: 1110px;
}

.app__aboutus-content_knife {
  margin: 6rem 4rem;
}

.app__aboutus-content_history {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  
  text-align: left;
}

.app__aboutus-content_about p{
  margin: 1rem 0;
  color: var(--color-grey);
}

.app__aboutus-content_history p{
  margin: 1rem 0;
  color: var(--color-grey);
}

@media screen and (max-width: 2000px) {
}

@media screen and (max-width: 900px) {
  .app__aboutus-content_knife img {
    height: 800px;
  }
  .app__aboutus-content_knife {
    margin: 4rem 2rem;
  }
}

@media screen and (max-width: 650px) {
  .app__aboutus-content {
    flex-direction: column;
  }

  .app__aboutus-overlay img {
    max-width: 250px;
    height: auto;
  }
}